* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}
.footer {
    width: 100%;
    padding: 30px 40px;
    background: #f6f6f6;
}
.footer .container {
    display: flex;
    max-width: 100%;
}
.footer .container .left-box {
    width: 35%;
    padding: 20px 20px;
    border-right: 2px solid #b8b7b7;
}
.footer .container .left-box .box {
    display: flex;
    padding: 10px 20px;
    margin-top: 15px;
}
.footer .container .left-box .box .icon-box {
    font-size: 26px;
    color: #cd1e76;
}
.footer .container .left-box .box .detail {
    margin-left: 10px;
}
.footer .container .left-box .box .detail h3 {
    text-transform: uppercase;
    font-size: 16px;
    color: #cd1e76;
    font-weight: 600;
    letter-spacing: 1px;
}
.footer .container .left-box .box .detail p {
    font-size: 14px;
    color: #232323;
    margin-top: 5px;
}
.footer .container .right-box {
    padding: 10px 20px;
    width: 64%;
}
.footer .container .right-box .header {
    max-width: 100%;
    align-items: center;
}
.footer .container .right-box .header img {
    margin-left: 40%;
}
.footer .container .right-box .header p {
    margin-top: 15px;
    font-size: 14px;
    text-align: center;
    font-weight: 600;
    color: #8a8a8a;
}
.footer .container .right-box .bottom {
    padding: 20px 20px;
    margin-top: 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.footer .container .right-box .bottom .box {
    padding: 10px 20px;
}
.footer .container .right-box .bottom .box h3 {
    text-transform: uppercase;
    color: #232323;
    font-size: 16px;
    /* letter-spacing: 1px; */
}
.footer .container .right-box .bottom .box ul {
    margin-top: 10px;
}
.footer .container .right-box .bottom .box ul li {
    list-style: none;
    margin-top: 8px;
    color: #8a8a8a;
    /* letter-spacing: 1px; */
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: 0.5s;
}
.footer .container .right-box .bottom .box ul li:hover {
    color: #cd1e76;
    letter-spacing: 1px;
}